@font-face {
  font-family: "OCRF-Regular";
  src: url("../fonts/ocrf-regularc-webfont.woff") format("woff"), url("../fonts/ocrf-regularc-webfont.woff2") format("woff2"), url("../fonts/ocrf-regularc.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.38;
  font-family: "OCRF-Regular", "Helvetica", "Arial", sans-serif; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 950px;
  margin: 0 auto; }

.page-main__promo {
  margin-bottom: 72px; }

.header {
  padding: 18px 40px 10px 63px; }

.header__wrapper {
  display: flex; }

.header__logo {
  margin-right: auto;
  display: flex;
  align-items: center; }

.header__logo-link:first-child {
  margin-right: 26px; }

.main-nav__list {
  list-style: none;
  display: flex; }

.main-nav__item:not(:last-child) {
  margin-right: 85px; }

.main-nav__link {
  color: #000000;
  font-size: 20px; }
  .main-nav__link:hover {
    opacity: 0.7; }

.promo {
  background-color: #fee600; }

.promo__wrapper {
  background: url("../img/promo.png") no-repeat;
  height: 501px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 30px; }

.promo__link {
  display: inline-block;
  text-align: center;
  min-width: 200px;
  letter-spacing: 2px;
  border-radius: 10px;
  padding: 15px 0;
  margin-right: 15px;
  background-color: #2b2d33;
  color: #ffffff; }

.about__decription {
  margin-bottom: 40px; }

.decription__wrapper {
  padding-left: 155px;
  padding-right: 45px; }

.decription__block {
  background-color: #fee600;
  border-radius: 40px;
  padding: 12px 30px; }
  .decription__block:nth-child(2) {
    margin-top: 20px;
    padding-bottom: 40px; }

.decription__text {
  line-height: 1.1; }

.features {
  background-color: #2b2d33; }

.features__wrapper {
  padding-top: 18px;
  padding-left: 80px; }

.features__wrapper > .features__title {
  text-align: center; }

.features__list {
  list-style: none;
  max-width: 750px; }

.features__item {
  margin-bottom: 42px; }

.features__title {
  font-weight: normal;
  color: white;
  text-transform: uppercase;
  margin: 0; }

.features__text {
  line-height: 1;
  color: #fee600;
  margin: 0; }

.for-whom__wrapper {
  padding: 0 100px; }

.for-whom__title {
  text-transform: uppercase;
  text-align: center; }

.for-whom__list {
  max-width: 500px;
  list-style: square outside;
  margin-left: auto; }

.for-whom__text {
  line-height: 1.1;
  margin: 0; }

.program__wrapper {
  width: 1100px;
  margin: 0 auto; }

.program__title {
  text-align: center;
  font-size: 16px;
  letter-spacing: 2px; }

.program__block {
  text-align: center; }

.program__link {
  display: inline-block;
  text-align: center;
  min-width: 200px;
  letter-spacing: 2px;
  border-radius: 10px;
  padding: 15px 0;
  margin-right: 15px;
  background-color: #fee600;
  color: #000000; }

.day {
  text-align: center; }

.day__title {
  font-size: 18px;
  display: inline-block;
  border-radius: 10px;
  background-color: #000000;
  color: #fee600;
  padding: 15px 25px;
  text-align: center; }

.day__list {
  list-style: none;
  padding: 0; }

.day__item {
  display: flex;
  margin-bottom: 10px;
  height: 50px; }

.day__item-name {
  width: 250px;
  margin: 0;
  margin-right: 30px;
  text-align: right; }

.day__item-span {
  background-color: #fee600;
  padding: 5px 20px; }

.day__item-text {
  margin: 0;
  align-self: flex-end; }

.speaker__wrapper {
  text-align: center; }

.speaker__title {
  font-size: 18px;
  font-weight: normal;
  letter-spacing: 1.5px;
  display: inline-block;
  border-radius: 10px;
  background-color: #fee600;
  color: #000000;
  padding: 15px 50px;
  text-align: center; }

.speaker__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  list-style: none; }

.speaker__item {
  width: 250px;
  height: 250px;
  position: relative;
  margin-bottom: 70px; }
  .speaker__item::before {
    position: absolute;
    top: -10px;
    right: -20px;
    content: "";
    width: 210px;
    height: 210px;
    border-radius: 50%;
    background-color: #fee600;
    z-index: -1; }

.speaker__img {
  border: 7px dashed #000000;
  border-radius: 50%;
  padding: 5px; }

.speaker__name {
  margin: 0;
  line-height: 1; }

.speaker__work {
  opacity: 0.4; }
